import { Component, OnInit, NgZone } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthenticationService } from '../userservices/authentication.service';
import { first } from 'rxjs/operators';
import { AlertService } from 'src/app/utils/alert/alert.service';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  signupForm:FormGroup;
  loading = false;
  submitted = false;
  returnUrl: string;
  
  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private authenticationService: AuthenticationService,
    private alertService:AlertService,
    private zone: NgZone
  ) { }

  ngOnInit() {
    this.signupForm = this.formBuilder.group({
      firstname: ['', Validators.required],
      lastname: ['', Validators.required],
      email: ['', [Validators.email, Validators.required]],
      password: ['', Validators.required],
      mobilenumber: ['', [Validators.required, Validators.minLength(10)]],
      address: ['', Validators.required],
      country: ['', Validators.required]
    });
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/home';
  }

  get f() { return this.signupForm.controls; };

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.signupForm.invalid) {
      return;
    }
    this.loading = true;

    this.authenticationService.signUp(this.f.firstname.value, this.f.lastname.value, this.f.email.value, this.f.password.value, this.f.mobilenumber.value, this.f.address.value, this.f.country.value)
      .pipe(first())
      .subscribe(
        data => {
          if(data['success']){
            this.alertService.success(data['ops']);
            this.zone.run(() => { 
              this.router.navigate([this.returnUrl]); 
            })
          } else {
            this.alertService.error(data['ops']);
            this.loading = false;
          }
        },
        error => {
          this.alertService.error(error);
          this.loading = false;
      }
    );
  }
}
