import { Component, OnInit, NgZone } from '@angular/core';
import { FormBuilder, FormGroup, Validators,ReactiveFormsModule } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthenticationService } from '../userservices/authentication.service';
import { first } from 'rxjs/operators';
import { AlertService } from 'src/app/utils/alert/alert.service';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.css']
})
export class SigninComponent implements OnInit {

  signinForm:FormGroup;
  loading = false;
  submitted = false;
  returnUrl: string;
  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private authenticationService: AuthenticationService,
    private alertService:AlertService,
    private zone: NgZone
  ) { 
    if (this.authenticationService.currentUserValue) { 
      this.router.navigate(['/home']);
    }
   }

//private authenticationService: AuthenticationService,
//private alertService: AlertService

  ngOnInit() {
    this.signinForm = this.formBuilder.group({
        username: ['', Validators.required],
        password: ['', Validators.required]
    });
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/home';
  }

  get f() { return this.signinForm.controls; };

  onSubmit() {
    this.submitted = true;

    if (this.signinForm.invalid) {
      return;
    }
    this.loading = true;
    this.authenticationService.signIn(this.f.username.value, this.f.password.value)
      .pipe(first())
      .subscribe(
        data => {
          if(data['success']){
            this.alertService.success(data['ops']);
            this.zone.run(() => { 
              this.router.navigate([this.returnUrl]); 
            })
          } else {
            this.alertService.error(data['ops']);
            this.loading = false;
          }
        },
        error => {
          this.alertService.error(error);
          this.loading = false;
      }
    );
  }
}
